import { Component, Input, OnChanges} from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Project } from '../class/project';
import { Utils } from '../class/utils';

@Component({
  selector: 'chart',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.css']
})

export class ChartComponent implements OnChanges {
  @Input() project: Project;
  globalDateStart: string;
  globalDateEnd: string;
  load = false;
  loadSpinner = false;
  isVerify: boolean = false;

  constructor(private database: AngularFireDatabase, private http: Http,
              private afAuth: AngularFireAuth) {}

  ngOnChanges() {
    this.globalDateStart = Utils.transformTimestampToDate(Date.now() - (7 * 24 * 3600 * 1000));
    this.globalDateEnd = Utils.transformTimestampToDate(Date.now());
    if(this.project.name !== 'Global') {
      this.load = false;
      this.loadSpinner = false;
      this.database.list(`/project-verifications/${this.project.id}`, {
        query: {
          limitToFirst: 1
        }
      }).subscribe(data => {
        this.isVerify = (data.length !== 0);
      });
      this.database.list('/project-users/' + this.project.id)
        .subscribe(data => {
          this.project.listUsers = [];
          data.forEach(user => {
            this.project.listUsers.push(user.userId);
          });
          this.project.listUsers.sort();
          this.load = true;
          this.loadSpinner = true;
        });
    }
    else {
      this.load = true;
      this.loadSpinner = true;
      this.isVerify = true;
    }
  }

  changeLastWeekDate(): void {
    this.globalDateStart = Utils.transformTimestampToDate(Date.now() - (7 * 24 * 3600 * 1000));
    this.globalDateEnd = Utils.transformTimestampToDate(Date.now());
  }

  changeLastMonthDate(): void {
    this.globalDateStart = Utils.transformTimestampToDate(Date.now() - (30 * 24 * 3600 * 1000));
    this.globalDateEnd = Utils.transformTimestampToDate(Date.now());
  }

  changeDate(): void {
    if(new Date(this.globalDateEnd) < new Date(this.globalDateStart)){
      const tmp = this.globalDateStart;
      this.globalDateStart = this.globalDateEnd;
      this.globalDateEnd = tmp;
    }
  }

  sendApi(): void {
    this.afAuth.auth.currentUser.getIdToken(true).then(token => {
      let headers = new Headers({'Content-Type': 'application/json', 'Authorization': `Bearer ${token}`});
      let options = new RequestOptions({headers: headers});
      this.loadSpinner = false;
      this.http.post('https://send-email-dot-simprints-dev.appspot.com/sendEmail', JSON.stringify({
        subject: "New project",
        receiver: this.afAuth.auth.currentUser.email,
        text: `This is the apiKey of your project: ${this.project.apiKey} for the project ${this.project.name}`
      }), options)
        .map(response => {
          return response;
        }).subscribe(() => {
        this.loadSpinner = true;
        alert(`You will receive an email at ${this.afAuth.auth.currentUser.email} with your apiKey`);
      });
    });
  }
}



