import {Component, Input, OnChanges } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { DatePipe } from '@angular/common';
import { Project } from "../class/project";

@Component({
  selector: 'list-error',
  templateUrl: './list-error.component.html',
  styleUrls: ['./list-error.component.css'],
  providers: [DatePipe]
})

export class ListErrorComponent implements OnChanges{
  @Input() project: Project;
  @Input() dateStart: string;
  @Input() dateEnd: string;
  @Input() list: string[];
  @Input() user: string;
  listError: any[] = [];

  constructor(private database: AngularFireDatabase, private datePipe: DatePipe){}

  ngOnChanges() {
    console.log(this.list);
    let url: string;
    if(this.project.name === 'Global')
      url = 'all-errors';
    else
      url = `project-errors/${this.project.id}`;
    this.listError = [];
    this.database.list(url, {
      query: {
        orderByChild: 'serverCreatedAt',
        startAt: new Date(this.dateStart).getTime(),
        endAt: new Date(this.dateEnd).getTime()
      }
    })
      .subscribe(data => {
        data.forEach((error: any) => {
          if(this.list.includes(error['name'])){
            if(this.user === 'All' || this.user === error['userId']) {
              let newError = {};
              newError['Time'] = this.datePipe.transform(error['serverCreatedAt']);
              newError['User'] = error['userId'];
              newError['Name'] = error['name'];
              newError['Project'] = error['projectId'];
              newError['Module'] = error['moduleId'];
              newError['Version'] = error['appVersion'];
              this.listError.push(newError);
            }
          }
        });
        this.listError.reverse();
      });
  }

  exportCSV(): void {
    const options = {
      fieldSeparator: ';',
      quoteStrings: '"',
      decimalseparator: '.',
      showLabels: true,
      useBom: true
    };
    new Angular2Csv(this.listError, 'ListError', options);
  }
}



