import { Component, Input, OnChanges, ViewChild } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { BaseChartDirective } from 'ng2-charts/ng2-charts';
import { ModalService} from "../modal/modal.service";
import { Observable } from 'rxjs';
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import { isUndefined } from "util";

@Component({
  selector: 'project-error',
  templateUrl: './project-error.component.html',
  styleUrls: ['./project-error.component.css']
})

export class ProjectErrorComponent implements OnChanges{
  @Input() project: Project;
  @Input() dateStartGlobal: string;
  @Input() dateEndGlobal: string;
  @ViewChild(BaseChartDirective) chart: BaseChartDirective;
  displayProjectError = true;
  dateStart: string;
  dateEnd: string;
  chooseUser: string[] = [];
  blueErrorCount: number;
  yellowErrorCount: number;
  redErrorCount: number;
  greyErrorCount: number;
  displayChart = false;
  allData: any;
  typeListError: string[] = [];

  constructor(private database: AngularFireDatabase, private modalService: ModalService) {}

  ngOnChanges() {
    this.chooseUser[0] = 'All';
    this.allData = {};
    this.initVal();
  }

  initVal(): void {
    this.dateStart = this.dateStartGlobal;
    this.dateEnd = this.dateEndGlobal;
    this.loadData();
  }

  loadData(): void {
    let url: string;
    if(this.project.name !== 'Global') {
      if (this.chooseUser[0] === 'All')
        url = `dashboard/project/${this.project.id}/project-data`;
      else
        url = `dashboard/project/${this.project.id}/user-data/users/ID-${this.chooseUser[0]}`;
    }
    else
      url = 'dashboard/global';
    Observable.forkJoin(
      this.database.list(`${url}/LOW_BATTERY/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/BLUETOOTH_NOT_ENABLED/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/MULTIPLE_PAIRED_SCANNERS/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/NOT_PAIRED/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/DISCONNECTED/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/UNEXPECTED_ERROR/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/MISSING_MODULE_ID/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/INVALID_API_KEY/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/MISSING_USER_ID/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/GUID_NOT_FOUND_OFFLINE/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/GUID_NOT_FOUND_ONLINE/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/UNVERIFIED_API_KEY/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1)
    ).subscribe(data => {
      this.transformErrorData(data);
      this.calculateValue();
      this.createGraph();
    });
  }

  calculateValue(): void {
    this.blueErrorCount = this.calculateOneValue(Utils.listBlueError);
    this.redErrorCount = this.calculateOneValue(Utils.listRedError);
    this.yellowErrorCount = this.calculateOneValue(Utils.listYellowError);
    this.greyErrorCount = this.calculateOneValue(Utils.listGreyError);
  }

  calculateOneValue(tab: string[]): number {
    let nb = 0;
    for(let i = 0; i < tab.length; i++){
      if(!isUndefined(this.allData[tab[i]])) {
        if (!isUndefined(this.allData[tab[i]][this.dateEnd])) {
          if (!isUndefined(this.allData[tab[i]][this.dateStart]))
            nb += this.allData[tab[i]][this.dateEnd]['ToDate'] - this.allData[tab[i]][this.dateStart]['ToDate'];
          else
            nb += this.allData[tab[i]][this.dateEnd]['ToDate'];
        }
      }
    }
    return nb;
  }

  transformErrorData(tab: any[0]): void {
    this.allData['LOW_BATTERY'] = Utils.transformTab(tab[0]);
    this.allData['BLUETOOTH_NOT_ENABLED'] = Utils.transformTab(tab[1]);
    this.allData['MULTIPLE_PAIRED_SCANNERS'] = Utils.transformTab(tab[2]);
    this.allData['NOT_PAIRED'] = Utils.transformTab(tab[3]);
    this.allData['DISCONNECTED'] = Utils.transformTab(tab[4]);
    this.allData['UNEXPECTED_ERROR'] = Utils.transformTab(tab[5]);
    this.allData['MISSING_MODULE_ID'] = Utils.transformTab(tab[6]);
    this.allData['INVALID_API_KEY'] = Utils.transformTab(tab[7]);
    this.allData['MISSING_USER_ID'] = Utils.transformTab(tab[8]);
    this.allData['GUID_NOT_FOUND_OFFLINE'] = Utils.transformTab(tab[9]);
    this.allData['GUID_NOT_FOUND_ONLINE'] = Utils.transformTab(tab[10]);
    this.allData['UNVERIFIED_API_KEY'] = Utils.transformTab(tab[11]);
  }

  createGraph(): void {
    let dateArray = Utils.computeDateArray(this.dateStart, this.dateEnd);
    this.lineChartData = [];
    let max = 0;
    if(this.blueErrorCount !== 0) {
      let data = this.computeNewValueGraph(dateArray, Utils.listBlueError);
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Blue errors', borderColor: 'rgb(39, 170, 225)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    if(this.yellowErrorCount !== 0){
      let data = this.computeNewValueGraph(dateArray, Utils.listYellowError);
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Yellow errors', borderColor: 'rgb(247, 138, 32)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    if(this.redErrorCount !== 0){
      let data = this.computeNewValueGraph(dateArray, Utils.listRedError);
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Red errors', borderColor: 'rgb(231, 95, 156)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    if(this.greyErrorCount !== 0){
      let data = this.computeNewValueGraph(dateArray, Utils.listGreyError);
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Grey errors', borderColor: 'rgb(116, 77, 155)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    this.lineChartOptions['scales']['yAxes'][0]['ticks']['max'] = max + 1;
    if(this.lineChartData.length === 0)
      this.displayChart = false;
    else {
      setTimeout(() => {
        if (this.chart && this.chart.chart && this.chart.chart.config) {
          this.chart.chart.config.data.labels = dateArray;
          this.chart.chart.config.data.datasets = this.lineChartData;
          this.chart.chart.options.scales.yAxes[0].ticks.max = max + 1;
          this.chart.chart.update();
        }
      });
      this.displayChart = true;
    }
  }

  computeNewValueGraph(dateArray: string[], errorNames: string[]): number[] {
    let graph: number[] = [];
    for(let j = 0; j < dateArray.length; j++){
      let nb = 0;
      for(let i = 0; i < errorNames.length; i++){
        if(!isUndefined(this.allData[errorNames[i]])) {
          if (!isUndefined(this.allData[errorNames[i]][dateArray[j]])) {
            nb += (this.allData[errorNames[i]][dateArray[j]]['ThisDay'] || 0);
          }
        }
      }
      graph.push(nb);
    }
    return graph;
  }

  applyFilter(): void {
    this.loadData();
  }

  changeDate(): void {
    if(new Date(this.dateEnd) < new Date(this.dateStart)){
      const tmp = this.dateStart;
      this.dateStart = this.dateEnd;
      this.dateEnd = tmp;
    }
    this.applyFilter();
  }

  openModal(id: string, choice: string){
    this.modalService.open(id);
    if(choice === 'blue')
      this.typeListError = Utils.listBlueError;
    else if(choice === 'yellow')
      this.typeListError = Utils.listYellowError;
    else if(choice === 'red')
      this.typeListError = Utils.listRedError;
    else
      this.typeListError = Utils.listGreyError;
  }

  closeModal(id: string){
    this.modalService.close(id);
  }

  public lineChartLegend:boolean = true;
  public lineChartType:string = 'line';
  public lineChartData:Array<any>;
  public lineChartLabels:Array<any>;
  public lineChartOptions:any = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true
        }
      }]
    }
  };
}



