import { Component, Input, OnChanges, ViewChild } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { BaseChartDirective } from 'ng2-charts/ng2-charts';
import { Observable } from 'rxjs';
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import { isUndefined } from "util";

@Component({
  selector: 'project-impact',
  templateUrl: './project-impact.component.html',
  styleUrls: ['./project-impact.component.css']
})

export class ProjectImpactComponent implements OnChanges{
  @Input() project: Project;
  @Input() dateStartGlobal: string;
  @Input() dateEndGlobal: string;
  @ViewChild(BaseChartDirective) chart: BaseChartDirective;
  displayProjectImpact = true;
  dateStart: string;
  dateEnd: string;
  listActivity: string[];
  chooseActivity: boolean[] = [];
  chooseUser: string[] = [];
  patientCount: number;
  idCount: number;
  userCount: number;
  verifyCount: number;
  patientCountGlobal: number;
  idCountGlobal: number;
  userCountGlobal: number;
  verifyCountGlobal: number;
  displayChart = false;

  constructor(private database: AngularFireDatabase) {}

  ngOnChanges() {
    this.listActivity = ['Identifications', 'Enrollments', 'Verifications'];
    this.chooseActivity = [true, true, true];
    this.chooseUser[0] = 'All';
    this.initVal();
    this.loadDataGlobal();
  }

  initVal(): void {
    this.dateStart = this.dateStartGlobal;
    this.dateEnd = this.dateEndGlobal;
    this.loadData();
  }

  loadDataGlobal(): void {
    let url: string;
    if(this.project.name !== 'Global')
      url = `dashboard/project/${this.project.id}/project-data`;
    else
      url = 'dashboard/global';
    this.database.object(`${url}/identifications/dates/${Utils.getTodayDate()}/ToDate`)
      .subscribe(data => {
        this.idCountGlobal = data.$value || 0;
      });
    this.database.object(`${url}/enrollments/dates/${Utils.getTodayDate()}/ToDate`)
      .subscribe(data => {
        this.patientCountGlobal = data.$value || 0; 
      });
    this.database.object(`${url}/verifications/dates/${Utils.getTodayDate()}/ToDate`)
      .subscribe(data => {
        this.verifyCountGlobal = data.$value || 0;
      });
    this.database.object(`${url}/users/dates/${Utils.getTodayDate()}/ToDate`)
      .subscribe(data => {
        this.userCountGlobal = data.$value || 0;
      });
  }

  loadData(): void {
    let url: string;
    if(this.project.name !== 'Global') {
      if (this.chooseUser[0] === 'All')
        url = `dashboard/project/${this.project.id}/project-data`;
      else
        url = `dashboard/project/${this.project.id}/user-data/users/ID-${this.chooseUser}`;
    }
    else
      url = 'dashboard/global';
    Observable.forkJoin(
      this.database.list(`${url}/identifications/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/enrollments/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/users/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/verifications/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1)
    ).subscribe(data => {
      this.calculateValue(data);
      this.createGraph(data);
    });
  }

  calculateValue(tab: any[]): void {
    if(tab[0].length > 0 && this.chooseActivity[0])
      this.idCount = this.calculateOneValue(Utils.transformTab(tab[0]));
    else
      this.idCount = 0;
    if(tab[1].length > 0 && this.chooseActivity[1])
      this.patientCount = this.calculateOneValue(Utils.transformTab(tab[1]));
    else
      this.patientCount = 0;
    if(tab[2].length > 0)
      this.userCount = this.calculateOneValue(Utils.transformTab(tab[2]));
    else
      this.userCount = 0;
    if(tab[3].length > 0 && this.chooseActivity[2])
      this.verifyCount = this.calculateOneValue(Utils.transformTab(tab[3]));
    else
      this.verifyCount = 0;
  }

  calculateOneValue(tab: any): number {
    if(!isUndefined(tab[this.dateEnd]))
      if(!isUndefined(tab[this.dateStart]))
        return (tab[this.dateEnd]['ToDate'] || 0) - (tab[this.dateStart]['ToDate'] || 0);
      else
        return (tab[this.dateEnd]['ToDate'] || 0);
    else
      return 0;

  }

  createGraph(tab: any[]): void {
    let dateArray = Utils.computeDateArray(this.dateStart, this.dateEnd);
    this.lineChartData = [];
    let max = 0;
    if(this.patientCount !== 0) {
      let data = this.computeNewValueGraph(dateArray, Utils.transformTab(tab[1]));
      max = Math.max(max, Math.max.apply(null, data));

      this.lineChartData.push({data: data, label: 'Enrollments', borderColor: 'rgb(231, 95, 156)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    if(this.idCount !== 0){
      let data = this.computeNewValueGraph(dateArray, Utils.transformTab(tab[0]));
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Identifications', borderColor: 'rgb(39, 170, 225)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    if(this.chooseUser[0] === 'All'){
      let data = this.computeNewValueGraph(dateArray, Utils.transformTab(tab[2]));
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Users', borderColor: 'rgb(247, 138, 32)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    if(this.verifyCount !== 0){
      let data = this.computeNewValueGraph(dateArray, Utils.transformTab(tab[3]));
      max = Math.max(max, Math.max.apply(null, data));
      this.lineChartData.push({data: data, label: 'Verifications', borderColor: 'rgb(139, 197, 68)',
        backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
        pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)'});
    }
    this.lineChartOptions['scales']['yAxes'][0]['ticks']['max'] = max + 1;
    //noinspection RedundantIfStatementJS
    if(this.verifyCount === 0 && this.idCount === 0 && this.patientCount === 0)
      this.displayChart = false;
    else
      this.displayChart = true;
    setTimeout(() => {
      if (this.chart && this.chart.chart && this.chart.chart.config) {
        this.chart.chart.config.data.labels = dateArray;
        this.chart.chart.config.data.datasets = this.lineChartData;
        this.chart.chart.options.scales.yAxes[0].ticks.max = max + 1;
        this.chart.chart.update();
      }
    });
  }


  computeNewValueGraph(dateArray: string[], data: any): number[]{
    let graph: number[] = [];
    for(let i = 0; i < dateArray.length; i++){
      if (isUndefined(data[dateArray[i]])) {
        graph.push(0);
      }
      else {
        graph.push(data[dateArray[i]]['ThisDay'] || 0);
      }
    }
    return graph;
  }

  applyFilter(): void {
    this.loadData();
  }

  changeDate(): void {
    if(new Date(this.dateEnd) < new Date(this.dateStart)){
      const tmp = this.dateStart;
      this.dateStart = this.dateEnd;
      this.dateEnd = tmp;
    }
    this.applyFilter();
  }

  public lineChartLegend:boolean = true;
  public lineChartType:string = 'line';
  public lineChartData:Array<any>;
  public lineChartLabels:Array<any>;
  public lineChartOptions:any = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true
        }
      }]
    }
  };
}



