import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {isUndefined} from "util";

@Component({
  selector: 'multiple-select',
  templateUrl: './multiple-select.component.html',
  styleUrls: ['./multiple-select.component.css']
})

export class MultipleSelectComponent implements OnInit {
  @Input() list: string[];
  @Input() choose: string[];
  @Input() listChecked: boolean[];
  @Input() displayTitle: boolean;
  @Input() getEvent: boolean;
  @Output() changeValue = new EventEmitter<void>();
  search: string = '';
  displayVal: boolean = false;
  checkAllVal: boolean = false;
  title: string;
  listPrivate: string[] = [];
  listPrivateObj: any[] = [];

  ngOnInit(): void {
    if(isUndefined(this.choose)) {
      this.checkAllVal = this.listChecked[0];
      for (let i = 1; i < this.listChecked.length; i++) {
        this.checkAllVal = this.checkAllVal && this.listChecked[i];
      }
      for(let i in this.list)
        this.listPrivateObj.push({value: this.list[i], index: i});
      if(this.displayTitle)
        this.changeTitle();
    }
    else {
      for(let i in this.list)
        this.listPrivate.push(this.list[i]);
      this.changeChoose('All');
    }
  }

  checked(index: number): void {
    this.listChecked[index] = !this.listChecked[index];
    this.checkAllVal = this.listChecked[0];
    for(let i = 1; i < this.listChecked.length; i++){
      this.checkAllVal = this.checkAllVal && this.listChecked[i];
    }
    if(this.displayTitle)
      this.changeTitle();
    this.emitEvent();
  }

  changeTitle(): void {
    if(this.checkAllVal)
      this.title = 'All';
    else {
      this.title = '';
      for (let i = 0; i < this.listChecked.length; i++){
        if(this.listChecked[i])
          this.title += this.list[i] + " ";
      }
    }
  }

  checkAll(): void{
    this.checkAllVal = !this.checkAllVal;
    for(let i = 0; i < this.listChecked.length; i++){
      this.listChecked[i] = this.checkAllVal;
    }
    if(this.displayTitle)
      this.changeTitle();
    this.emitEvent();
  }

  changeChoose(val: string): void {
    this.title = val;
    this.choose[0] = val;
    this.emitEvent();
  }

  emitEvent(): void {
    if(this.getEvent) {
      this.displayVal = false;
      this.changeValue.emit();
    }
  }

  searchValue(): void {
    if(isUndefined(this.choose)){
      this.listPrivateObj = [];
      for (let i in this.list) {
        if (this.list[i].search(this.search) !== -1)
          this.listPrivateObj.push({value: this.list[i], index: i});
      }
    }
  else {
      this.listPrivate = [];
      for (let i in this.list) {
        if (this.list[i].search(this.search) !== -1)
          this.listPrivate.push(this.list[i]);
      }
    }
  }
}



