import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { Http, Headers, RequestOptions } from '@angular/http';


@Component({
  selector: 'create-project',
  templateUrl: './create-project.component.html',
  styleUrls: ['./create-project.component.css']
})

export class CreateProjectComponent implements OnInit {
  project: any = {};
  test: boolean = true;
  load: boolean = false;
  url: string = "https://us-central1-simprints-dev.cloudfunctions.net/create_project/createProject";
  constructor(private http: Http, private afAuth: AngularFireAuth){}

  ngOnInit(): void {
    this.project.name = '';
    this.project.projectManager = '';
    this.project.contract = '';
    this.project.dateStart = '';
    this.project.dateEnd = '';
    this.project.details = '';
  }

  onSubmit(){
    this.afAuth.auth.currentUser.getIdToken(true).then(token => {
      this.project.test = this.test;
      let headers = new Headers({'Content-Type': 'application/json', 'Authorization': `Bearer ${token}`});
      let options = new RequestOptions({headers: headers});
      let data = {
        data: {
          Info: `create key for project ${this.project.name}`,
          Issuer: this.afAuth.auth.currentUser.displayName,
          Test: this.project.test
        },
        info: {
          projectManager: this.project.projectManager,
          projectName: this.project.name,
          contract: this.project.contract,
          dateStart: this.project.dateStart,
          dateEnd: this.project.dateEnd
        }
      };
      this.load = true;
      this.http.post(this.url, JSON.stringify(data), options)
        .map(response => {
          return response.json();
        }).subscribe(json => {
        this.http.post('https://send-email-dot-simprints-dev.appspot.com/sendEmail', JSON.stringify({
          subject: "New project",
          receiver: this.afAuth.auth.currentUser.email,
          text: `You create a new project called ${this.project.name}. This is the apiKey of your project: ${json.apiKey} 
            and this is the projectId ${json.projectId}`
        }), options)
          .map(response => {
            return response;
          }).subscribe(() => {
          this.load = false;
          alert(`You will receiver an email at ${this.afAuth.auth.currentUser.email} with your apiKey and projectId`);
        });
      });
    });
  }
}



