import { Component, Input, OnChanges, ViewChild } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { BaseChartDirective } from 'ng2-charts/ng2-charts';
import { DecimalPipe } from '@angular/common';
import { Observable } from 'rxjs';
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import { isUndefined } from "util";

@Component({
  selector: 'project-performance',
  templateUrl: './project-performance.component.html',
  styleUrls: ['./project-performance.component.css'],
  providers: [DecimalPipe]
})

export class ProjectPerformanceComponent implements OnChanges{
  @Input() project: Project;
  @Input() dateStartGlobal: string;
  @Input() dateEndGlobal: string;
  @Input() type: string;
  @ViewChild(BaseChartDirective) chart: BaseChartDirective;
  title: string;
  displayProjectPerformance = true;
  dateStart: string;
  dateEnd: string;
  chooseUser: string[] = [];
  dailyUser: number;
  monthlyUser: number;
  matchSpeed: number;
  accuracy: number = 0;
  displayChart = false;
  total: number = 0;

  constructor(private database: AngularFireDatabase, private decimalPipe: DecimalPipe) {}

  ngOnChanges() {
    if(this.type === "IDENTIFY") {
      this.title = "Identifications";
      this.lineChartOptionsAccuracy = {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [{
            scaleLabel: {
              display: true,
              labelString: 'TPIR rank'
            }
          }],
          yAxes: [{
            scaleLabel: {
              display: true,
              labelString: 'Percent of identifications'
            },
            ticks: {
              beginAtZero: true
            }
          }]
        }
      };
    }
    else {
      this.title = "Verifications";
      this.lineChartOptionsAccuracy = {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          xAxes: [{
            scaleLabel: {
              display: true,
              labelString: 'Tiers rank'
            }
          }],
          yAxes: [{
            scaleLabel: {
              display: true,
              labelString: 'Percentage'
            },
            ticks: {
              beginAtZero: true
            }
          }]
        }
      };
    }
    this.chooseUser[0] = 'All';
    this.initVal();
  }

  initVal(): void {
    this.dateStart = this.dateStartGlobal;
    this.dateEnd = this.dateEndGlobal;
    this.loadData();
  }

  loadData(): void {
    let url: string;
    if(this.project.name !== 'Global') {
      if (this.chooseUser[0] === 'All')
        url = `dashboard/project/${this.project.id}/project-data`;
      else
        url = `dashboard/project/${this.project.id}/user-data/users/ID-${this.chooseUser}`;
    }
    else
      url = 'dashboard/global';
    Observable.forkJoin(
      this.database.list(`${url}/${this.type}-time-stats/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`dashboard/project/${this.project.id}/project-data/daily-active-users/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/${this.type}-accuracy/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1)
    ).subscribe(data => {
      this.calculateValue(data);
      this.createGraph(data);
    });
  }

  calculateValue(tab: any[]): void {
    if(tab[0].length > 0) {
      tab[0] = Utils.transformTab(tab[0]);
      if(!isUndefined(tab[0][this.dateEnd]))
        if(!isUndefined(tab[0][this.dateStart]))
          this.matchSpeed = (((tab[0][this.dateEnd]['matching-time']['averageToDate'] || 0) * (tab[0][this.dateEnd]['matching-time']['numberToDate'] || 0)
            - (tab[0][this.dateStart]['matching-time']['averageToDate'] || 0) * (tab[0][this.dateStart]['matching-time']['numberToDate'] || 0))
            / ((tab[0][this.dateEnd]['matching-time']['numberToDate'] || 0) -
              (tab[0][this.dateStart]['matching-time']['numberToDate'] || 0))) / 1000;
        else
          this.matchSpeed = (((tab[0][this.dateEnd]['matching-time']['averageToDate'] || 0) * (tab[0][this.dateEnd]['matching-time']['numberToDate'] || 0))
            / (tab[0][this.dateEnd]['matching-time']['numberToDate'] || 0)) / 1000;
      else
        this.matchSpeed = 0;
      if(isNaN(this.matchSpeed))
        this.matchSpeed = 0;
    }
    else
      this.matchSpeed = 0;
    this.dailyUser = this.countDailyUser(Utils.transformTab(tab[1]), this.dateEnd);
    this.monthlyUser = this.countPeriodUser(Utils.computeDateArray(this.dateStart, this.dateEnd), Utils.transformTab(tab[1]));
    if(this.type === "IDENTIFY")
      this.accuracy = this.calculateAccuracyIdentify(Utils.transformTab(tab[2]));
    else
      this.accuracy = this.calculateAccuracyVerify(Utils.transformTab(tab[2]));
  }

  calculateAccuracyIdentify(data: any): number {
    let val: number = 0;
    for(let i = 0; i < 20; i++){
      if(!isUndefined(data[this.dateEnd])) {
        if(!isUndefined(data[this.dateStart]))
          val += (data[this.dateEnd][`${i}ToDate`] || 0) - (data[this.dateStart][`${i}ToDate`] || 0);
        else
          val += (data[this.dateEnd][`${i}ToDate`] || 0);
      }
    }
    let total: number = 0;
    if(!isUndefined(data[this.dateEnd])){
      if(!isUndefined(data[this.dateStart]))
        total = (data[this.dateEnd][`totalToDate`] || 0) - (data[this.dateStart][`totalToDate`] || 0);
      else
        total = (data[this.dateEnd][`totalToDate`] || 0);
      this.total = total;
      if(total === 0)
        total = 1;
      return (val / total) * 100;
    }
    else
      return val;
  }

  calculateAccuracyVerify(data: any): number {
    let val = 0;
    for(let i = 1; i < 5; i ++) {
      if (!isUndefined(data[this.dateEnd])) {
        if (!isUndefined(data[this.dateStart]))
          val += (data[this.dateEnd][`${i}ToDate`] || 0) - (data[this.dateStart][`${i}ToDate`] || 0);
        else
          val += (data[this.dateEnd][`${i}ToDate`] || 0);
      }
    }
    let nbLastTier: number = 0;
    if(!isUndefined(data[this.dateEnd])){
      if(!isUndefined(data[this.dateStart]))
        nbLastTier = (data[this.dateEnd][`5ToDate`] || 0) - (data[this.dateStart][`5ToDate`] || 0);
      else
        nbLastTier = (data[this.dateEnd][`5ToDate`] || 0);
      this.total = val + nbLastTier;
      if(this.total === 0)
        return 0;
      else
        return (val / (val + nbLastTier)) * 100;
    }
    else
      return val;
  }

  countPeriodUser(dateArray: string[], data: any): number {
    let user = {};
    let nb = 0;
    for (let i = 0; i < dateArray.length; i++) {
      if (!isUndefined(data[dateArray[i]])) {
        for (let key in data[dateArray[i]]) {
          user[key] = true;
        }
      }
    }
    for (let key in user) {
      if(this.chooseUser[0] === 'All' || this.chooseUser[0] === key)
        nb++;
    }
    return nb;
  }

  countDailyUser(data: any, date: string): number {
    if(isUndefined(data[date]))
      return 0;
    else{
      let nb = 0;
      for(let key in data[date]) {
        if(this.chooseUser[0] === 'All' || this.chooseUser[0] === key)
          nb++;
      }
      return nb;
    }
  }

  createGraph(data: any[]): void {
    let dateArray = Utils.computeDateArray(this.dateStart, this.dateEnd);
    this.lineChartData = [];
    if(this.type === "IDENTIFY")
      this.lineChartLabelsAccuracy = Array.from(Array(10), (e, i) => i + 1);
    else
      this.lineChartLabelsAccuracy = Array.from(Array(5), (e, i) => i + 1);
    this.lineChartDataAccuracy = [];
    if(this.type === "IDENTIFY")
      this.lineChartDataAccuracy.push({data: this.computeNewValueAccuracyGraphIdentify(Utils.transformTab(data[2])), label: 'Accuracy'});
    else
      this.lineChartDataAccuracy.push({data: this.computeNewValueAccuracyGraphVerify(Utils.transformTab(data[2])), label: 'Confidence'});
    let dataUser = this.computeNewValueUserGraph(dateArray, Utils.transformTab(data[1]));
    let dataAverage = this.computeNewValueAverageGraph(dateArray, data[0]);
    this.lineChartData.push({data: dataUser, label: 'Daily User', borderColor: 'rgb(39, 170, 225)',
      backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)', yAxisID: 'y-axis-0'});
    this.lineChartData.push({data: dataAverage, label: 'Matching Speed', borderColor: 'rgb(231, 95, 156)',
      backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)', yAxisID: 'y-axis-1'});
    this.lineChartOptions['scales']['yAxes'][0]['ticks']['max'] = Math.max.apply(null, dataUser) + 1;
    this.lineChartOptions['scales']['yAxes'][1]['ticks']['max'] = Math.max.apply(null, dataAverage) + 1;
    setTimeout(() => {
      if (this.chart && this.chart.chart && this.chart.chart.config) {
        this.chart.chart.config.data.labels = dateArray;
        this.chart.chart.config.data.datasets = this.lineChartData;
        this.chart.chart.options.scales.yAxes[0].ticks.max = Math.max.apply(null, dataUser) + 1;
        this.chart.chart.options.scales.yAxes[1].ticks.max = Math.max.apply(null, dataAverage) + 1;
        this.chart.chart.update();
      }
    });
    this.displayChart = true;
  }

  computeNewValueAccuracyGraphIdentify(data: any): number[] {
    let graph = [];
    let total: number;
    if(!isUndefined(data[this.dateEnd])){
      if(!isUndefined(data[this.dateStart]))
        total = (data[this.dateEnd][`totalToDate`] || 0) - (data[this.dateStart][`totalToDate`] || 0);
      else
        total = (data[this.dateEnd][`totalToDate`] || 0);
      if(total === 0)
        total = 1;
    }
    let val = 0;
    for(let i = 0; i < 10; i++){
      if(!isUndefined(data[this.dateEnd])) {
        if(!isUndefined(data[this.dateStart]))
          val += (data[this.dateEnd][`${i}ToDate`] || 0) - (data[this.dateStart][`${i}ToDate`] || 0);
        else
          val += data[this.dateEnd][`${i}ToDate`] || 0;
        graph.push(this.decimalPipe.transform((val / total) * 100, '1.0-2'));
      }
    }
    return graph;
  }

  computeNewValueAccuracyGraphVerify(data: any): number[] {
    let graph = [];
    for(let i = 1; i < 6; i++){
      if(!isUndefined(data[this.dateEnd])) {
        if (!isUndefined(data[this.dateStart]))
          graph.push(this.decimalPipe.transform(((((data[this.dateEnd][`${i}ToDate`] || 0) - (data[this.dateStart][`${i}ToDate`] || 0)) /
            (this.total || 1)) * 100), '1.0-2'));
        else
          graph.push(this.decimalPipe.transform((((data[this.dateEnd][`${i}ToDate`] || 0) / (this.total || 1)) * 100), '1.0-2'));
      }
      else
        graph.push(0);
    }
    return graph;
  }

  computeNewValueAverageGraph(dateArray: string[], data: any): number[]{
    let graph: number[] = [];
    for(let i = 0; i < dateArray.length; i++){
      if(isUndefined(data[dateArray[i]]))
        graph.push(0);
      else
        graph.push((data[dateArray[i]]['matching-time']['averageThisDay'] || 0)/ 1000);
    }
    return graph;
  }

  computeNewValueUserGraph(dateArray: string[], data: any): number[]{
    let graph: number[] = [];
    for (let i = 0; i < dateArray.length; i++) {
      graph.push(this.countDailyUser(data, dateArray[i]));
    }
    return graph;
  }

  applyFilter(): void {
    this.loadData();
  }

  changeDate(): void {
    if(new Date(this.dateEnd) < new Date(this.dateStart)){
      const tmp = this.dateStart;
      this.dateStart = this.dateEnd;
      this.dateEnd = tmp;
    }
    this.applyFilter();
  }

  public lineChartLegend:boolean = true;
  public lineChartType:string = 'line';
  public lineChartData:Array<any>;
  public lineChartLabels:Array<any>;
  public lineChartOptions:any = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      xAxes: [{
        stacked: true
      }],
      yAxes: [{
        type: 'linear',
        position: "left",
        id: "y-axis-0",
        scaleLabel: {
          display: true,
          labelString: 'Number of user'
        },
        ticks: {
          beginAtZero: true
        }
      }, {
        type: 'linear',
        position: "right",
        id: "y-axis-1",
        scaleLabel: {
          display: true,
          labelString: 'Matching speed'
        },
        ticks: {
          beginAtZero: true
        }
      }]
    }
  };
  public lineChartLegendAccuracy:boolean = true;
  public lineChartTypeAccuracy:string = 'bar';
  public lineChartDataAccuracy:Array<any>;
  public lineChartLabelsAccuracy:Array<any>;
  public lineChartOptionsAccuracy:any;
  public lineChartColorAccuracy: Array<any> = [{
    backgroundColor: 'rgba(116, 77, 155, 0.5)',
    hoverBackgroundColor: 'rgba(116, 77, 155, 1)',
    borderColor: 'rgb(116, 77, 155)',
    borderWidth: 1
  }];
}



